const container = document.getElementById('root');
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
const NEWS_URL = 'http://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'http://api.hnpwa.com/v0/item/@id.json'

function getData(url) {
    ajax.open('GET', url, false);
    ajax.send();

    return JSON.parse(ajax.response);
}

// Í∏Ä Î™©Î°ù
function newsFeed(){
    const newsFeed = getData(NEWS_URL);
    const newsList = [];
    newsList.push('<ul>');
    for(let i=0; i<10; i++){
        newsList.push(`
        <li>
            <a href="#${newsFeed[i].id}">
            ${newsFeed[i].title} (${newsFeed[i].comments_count})
            </a>
        </li>
        `);
    }
    newsList.push('</ul>');

    container.innerHTML = newsList.join('');
}

const ul = document.createElement('ul');

// Í∏Ä ÎÇ¥Ïö©
function newsDetail(){
    const id = location.hash.substr(1);
    const newsContent = getData(CONTENT_URL.replace('@id', id));
    const title = document.createElement('h1');

    container.innerHTML = `
        <h1>${newsContent.title}</h1>
        <div>
            <a href="#">Î™©Î°ùÏúºÎ°ú</a>
        </div>
    `;
}

// ÎùºÏö∞ÌÑ∞
// ÌôîÎ©¥Ïù¥ Ï†ÑÌôòÎêòÏñ¥Ïïº Ìï† Îïå(hashchange) ÎùºÏö∞ÌÑ∞Í∞Ä ÌåêÎã®Ìï¥ÏÑú Ìï¥ÎãπÌïòÎäî ÌôîÎ©¥ÏúºÎ°ú Ï†ÑÌôòÏãúÌÇ¥
function router(){
    const routePath = location.hash;   // ÌôîÎ©¥ÏùÑ Ï†ÑÌôòÌïòÎäî Ïö©ÎèÑÏùò Í∞í

    // ÎùºÏö∞ÌÑ∞ Í∞íÏùÑ Í∞ÄÏßÄÍ≥† ÎÇ¥Í∞Ä ÌòÑÏû¨ Î™©Î°ùÍ≥º ÎÇ¥Ïö©Ï§ë Ïñ¥Îñ§Í≤ÉÏùÑ ÌëúÏãúÌï†ÏßÄ ÌåêÎã®Ìï¥ÏïºÌï®
    if(routePath === ''){
        // routePathÏóê Îπà Î¨∏ÏûêÏó¥Ïù¥Î©¥ location.hashÏóê #Ïù¥ Îì§Ïñ¥ÏûàÏùå
        // locatiohn.hashÏóê #Îßå ÏûàÏùÑ Í≤ΩÏö∞ Îπà Í∞íÏùÑ Î∞òÌôòÌïòÏó¨ Ï∞∏
        newsFeed();
    } else {
        newsDetail();
    }
}

// Ìï¥ÏãúÍ∞Ä Î∞îÎÄî ÎïåÎßàÎã§ ÎùºÏö∞ÌÑ∞Í∞Ä ÎèôÏûëÌï®
window.addEventListener('hashchange', router);

router();